      <Menu />
      <img class="responsive-img" src="@/assets/images/hr.png" alt="">
      <div class="wrpCarousel">
        <button class="btnCarousel" v-on:click='nextSlide'>&#8667;</button>
        <div class='slider js-slider'>
          <div class="slider__body" v-bind:style='{left: sliderOffsetLeft + "px"}'>
            <div class="sliderMain slider__slide js-slide" v-for="dataCar in carouselData" :key="dataCar.index">
              <img class="imgCarousel responsive-img" :src="require('@/assets/images/' + dataCar.image)" alt="">
              <h5>{{dataCar.title}}</h5>
              <p class="descriptionCarousel">{{dataCar.description}}</p>
              <hr> 
              <a class=" waves-effect lighten btn">See all</a>
            </div>
          </div>
        </div>
      </div>
      <Loader v-if="!clotheIs"/>
      <div v-else class="blockBar">
        <div class="container">
          <img class="responsive-img" src="@/assets/images/hr.png" alt="">
          <h3 class="titlesOfBlocks" id="discountTitle">Discaunt on clothes for women<span><img class="attencionImg" src="@/assets/images/att.png" alt=""></span></h3>
          <div class="offersMain wrpClotheBar">
            <ClotheItem
            class="product_bar_item"
            v-for="item in newClothes"
            :key="item.index"
            v-show="item.oferta && item.sex == 'female'"
            v-bind:productdata="item"
            />
          </div>
          <router-link to="/women" class="waves-effect lighten btn ofertabtn">See more..</router-link>
        </div>
      </div>
      <Loader v-if="!clotheIs"/>
      <div v-else class="blockBar">
        <div class="container">
          <img class="responsive-img" src="@/assets/images/hr.png" alt="">
          <h3 class="titlesOfBlocks" id="discountTitle">Discaunt on clothes for man<span><img class="attencionImg" src="@/assets/images/att.png" alt=""></span></h3>
          <div class="offersMain wrpClotheBar">
            <ClotheItem class="product_bar_item"
            v-for="item in newClothes"
            :key="item.index"
            v-show="item.oferta && item.sex == 'male'"
            v-bind:productdata="item"
            />
          </div>
          <router-link to="/man" class="waves-effect lighten btn ofertabtn">See more..</router-link>
        </div>
      </div>
      <div class="formWrp">
        <img class="responsive-img" src="@/assets/images/hr.png" alt="">
        <h4 class="titlesOfBlocks">Complete form and ask questions</h4>
        <FormBlock />
      </div>
      <div class="container">
        <img class="responsive-img" src="@/assets/images/hr.png" alt="">
        <div class="preimWrp">
          <div class="preimItem">
            <img src="@/assets/images/preim1.png" alt="">
            <div class="preimContent">
              <p>Free shipping</p>
              <p>On all orders just 49$</p>
            </div>
          </div>
          <div class="preimItem">
            <img src="@/assets/images/preim2.png" alt="">
            <div class="preimContent">
              <p>15 days returns</p>
              <p>On all orders just 49$</p>
            </div>
          </div>
          <div class="preimItem">
            <img src="@/assets/images/preim3.png" alt="">
            <div class="preimContent">
              <p>Secure checkout</p>
              <p>On all orders just 49$</p>
            </div>
          </div>
          <div class="preimItem">
            <img src="@/assets/images/preim4.png" alt="">
            <div class="preimContent">
              <p>100% free warinly</p>
              <p>On all orders just 49$</p>
            </div>
          </div>
        </div>
      </div>

      <Footer />
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'
import Footer from '@/components/Footer.vue'
import FormBlock from '@/components/FormBlock.vue'
import ClotheItem from '@/components/ClotheItem.vue'
import axios from 'axios'
import { bus } from '@/main.js'

const baseUrl = "http://localhost:3000/newClothes"

export default {
  name: 'Home',
  components: {
    Menu,
    Footer,
    FormBlock,
    ClotheItem
  },
  data() {
    return {
      carouselData: [
        {
          image: '1.jpg',
          title: 'New collection for women',
          description: 'New collection from our factories! Kumque reprehenderit eos, officia, totam delectus repellat. Similique error aperiam dolores soluta. Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aliquid consequuntur, placeat magnam at voluptatum culpa quae distinctio saepe.'
        },
        {
          image: '2.jpg',
          title: 'New collection for men',
          description: 'New collection from our factories! Kumque reprehenderit eos, officia, totam delectus repellat. Similique error aperiam dolores soluta. Lorem, ipsum dolor sit amet consectetur adipisicing elit. Aliquid consequuntur, placeat magnam at voluptatum culpa quae distinctio saepe.'
        }
       ],
      sliderAllCount: 0,
      sliderActive: 1,
      // Отступ тела со слайдами в контейнере
      sliderOffsetLeft: 0,
      // Шаг одного слайда = его длина
      sliderOffsetStep: 0,
      newClothes: [],
      currentid: '',
      linktocard: '/opencard/',
      clotheMan: [],
      loading: true,
      clotheIs: false
    }
  },
  async created() {
    try{
      const res = await axios.get(baseUrl);

      this.newClothes = res.data;
      this.clotheIs = true;
    } catch (e) {
      console.error(e);
    }
  },
  methods: {
    initSlider: function () {
    let sliderBody = this.$el.querySelector('.js-slider')
    let sliderSlidies = sliderBody.querySelectorAll('.js-slide')
    this.sliderOffsetStep = sliderBody.clientWidth
    this.sliderAllCount = sliderSlidies.length
    },
    openSlide: function (id) {
      if (id > 0 && id <= this.sliderAllCount) {
        this.sliderActive = id
        this.sliderOffsetLeft = -(this.sliderActive * this.sliderOffsetStep - this.sliderOffsetStep)
      }
    },
    nextSlide: function () {
      if (this.sliderActive < this.sliderAllCount) {
        this.sliderActive += 1
        this.openSlide(this.sliderActive)
      }
      else{
        this.sliderActive = 0
      }
    },
    prevSlide: function () {
        if (this.sliderActive > 1) {
          this.sliderActive -= 1
          this.openSlide(this.sliderActive)
        }
      },
    currencyid(id) {
      this.currentid = id
      console.log("currencyid")
      bus.$emit('currencyid', this.currentid)
    }
  },
  mounted () {
    this.initSlider()
    window.addEventListener('resize', () => {
      this.initSlider()
      this.openSlide(this.sliderActive)
    })
  },
  beforeMount() {
    setInterval(() => {
      this.nextSlide()
    }, 2500)
  },
  computed: {
    error() {
      return this.$store.getters.error
    }
  },
  watch: {
    error(fbError) {
      console.log(fbError)
    }
  }
}
</script>

